}


/* --------- Dropdown ---------- */
.drop-nav{
  position: relative;
  /* padding: 2px 5; */
  font-size: 1.2rem;
  height: 30px;
  margin-bottom: 16px;
}
.drop-nav a{
  text-transform: capitalize;
  overflow: hidden;
}
.drop-nav .dropdown{
  position: absolute;
  background: rgb(20, 19, 19);
  width: 100%;
  width: calc(100% + 30px);
  top: 30px;
  display:none;
  
}
.drop-nav .dropdown li{
  display: block;
}
.drop-nav:hover .dropdown{
  display: block;
}
/* ----------------------------- */

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 0px;
    margin-bottom: 15px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 50%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: rgb(20, 15, 15);
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  /* --------- Dropdown ---------- */
  .drop-nav{
    position: relative;
    padding: 10px 0;
  }
  .drop-nav a{
    text-transform: capitalize;
    overflow: hidden;
  }
  .drop-nav .dropdown{
    position: absolute;
    background: #282c34;
    width: 100%;
    width: calc(100% + 30px);
    top: 50px;
    display: none;
  }
  .drop-nav .dropdown li{
    display: block;
  }
  .drop-nav:hover .dropdown{
    display: block;
  }
  .drop-nav .nav-links{
    position: relative;
    padding: 10px 0;
  }
}
